import { Api } from "./api";
import { DateUtils } from "@/utils";

/**
 * Event 
 */
export class EventApi extends Api {
  static #eventUrl = "/events";

  //  index
  static get mainTagIndex() {
    return 0;
  }

  /**
   * 
   * @returns {Promise} 
   */
  static async fetchEvents() {
    let result;

    await this._axios
      .get(`${this.#eventUrl}?date_gte=${DateUtils.getTomorrow()}&_sort=date`)
      .then((res) => {
        result = res.data;
      })
      .catch((error) => {
        console.error(error);
      });

    return result;
  }

  /**
   *  banner 
   * @returns {Promise} 
   */
  static async fetchBanners() {
    let result;

    await this._axios
      .get(`${this.#eventUrl}?isBanner=true&_sort=date`)
      .then((res) => {
        result = res.data;
      })
      .catch((error) => {
        console.error(error);
      });

    return result;
  }

  /**
   *  ID
   * @param {Object} event 
   * @returns {number}  ID
   */
  static getMainTagId(event) {
    return event?.tags[this.mainTagIndex];
  }

  /**
   *  ID
   * @param {Object} event 
   * @returns {Array}  ID
   * @throws  tags 
   */
  static getOtherTagId(event) {
    const tags = event?.tags;
    if (!tags) {
      throw Error(" tags ");
    }
    return tags.slice(this.mainTagIndex + 1);
  }
}
