import { Api } from "./api";

/**
 * Tag 
 */
export class TagApi extends Api {
  static #tagUrl = "/tags";

  /**
   *  tag 
   * @returns {Promise} 
   */
  static async fetchTags() {
    let result;

    await this._axios
      .get(`${this.#tagUrl}`)
      .then((res) => {
        result = res.data;
      })
      .catch((error) => {
        console.error(error);
      });

    return result;
  }

  /**
   *  tag ID 
   * @param {Array} tags 
   * @returns {Object}  tag ID  {id: name}
   */
  static getIdMap(tags) {
    const idMap = tags.reduce((map, item) => {
      map[item.id] = item.name;
      return map;
    }, {});

    return idMap;
  }

  /**
   * 
   * @param {string} str 
   * @returns {Object}  tag 
   */
  static searchByName(tags, str) {
    // 
    let result = tags.find((item) => {
      return item.name === str;
    });

    // 
    if (!result) {
      // 
      result = tags.find((item) => {
        return item.name.includes(str);
      });
    }

    return result;
  }
}
